<template>
  <div id="product-one">
    <h1>Fruits List one</h1>
    <ul>
      <li v-for="p in product">
       <span class="name">{{p.name}}</span>
       <span class="price">${{p.price}}</span>
       </li>
     </ul> 
     <button @click="reduce" value="">Reduce Price</button>
    <!--<router-view></router-view>-->
  </div>
</template>

<script>
export default {
computed:{
  product(){
    return this.$store.state.products;
  },
sproduct(){
  return this.$store.getters.salesProducts;
}
 },

methods:{
reduce:function()
{
  //return this.$store.mutations.reduce;

  this.$store.commit('reduce');
}}
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



