import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export const vx=new Vuex.Store({
state:{
	products:[
    {name:'Apple',price:'100'},
    {name:'Orange',price:'50'},
    {name:'PineApple',price:'87'},
    {name:'Grapes',price:'76'},
    {name:'Mango',price:'70'}
    ]
},
getters:{
	salesProducts:state=>{
	var calc=state.products.map(p=>{
      return{
        name:'"'+p.name+'"',
        price:p.price/2
      }
    });
    return calc;}
},
mutations:{
	reduce:state=>{
		state.products.forEach(p=>{
p.price-=1;}
    );
	}
}
});


