<template>
  <div id="product-two">
    <h1>Fruits List two</h1>
    <ul>
      <li v-for="p in salesProducts">
        <span>{{p.name}}</span>
        <span>&{{p.price}}</span>
      </li>

     </ul> 
    <!--<router-view></router-view>-->
  </div>
</template>

<script>
export default {
computed:{
  products(){
    return this.$store.state.products;
  },
  salesProducts(){
    return this.$store.getters.salesProducts;
  }
}
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



